import React from "react";
import "./App.css";
import login from "./facebook-logo.png";

export default function Login() {
  return (
    <div>
        <div class="login-form">
        <img src={login} width="170px" height="25px" alt="alt" />
        <div class="form-group">
            <input type="text" class="form-control input-css" name="username" placeholder="Mobile number or email address" required="required"/>
        </div>
        <div class="form-group">
            <input type="password" class="form-control input-css" name="username" placeholder="Password" required="required"/>
        </div>
        <div class="form-group">
            <button className="btn btn-primary btn-login">Log In</button>
        </div>
        <div class="or-seperator"><i>or</i></div>
        <div class="form-group">
            <button className="btn btn-primary btn-create">Create New Account</button>
        </div>
        <div class="clearfix">
                <a href="#" class="float-right link">Forgotten Password?</a>
            </div>
        </div>
    </div>
  );
}
